/* Add this in your CSS file */
.placeholder-dark::placeholder {
    color: white;
  }
  
  .placeholder-light::placeholder {
    color: black;
  }
  /* Default styles for larger screens */
body {
  font-size: 18px;
  padding: 20px;
}

/* Mobile screen styles */
@media screen and (max-width: 768px) {
  body {
    font-size: 16px;
    padding: 10px;
  }

  /* Adjust other styles for mobile here */
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
  }
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

@media screen and (max-width: 768px) {
  .grid-container {
    grid-template-columns: repeat(1, 1fr);
  }
}

.button-container {
  display: flex;
  flex-direction: row; /* By default, buttons will align horizontally */
  justify-content: center; /* Horizontally center the buttons */
  gap: 20px; /* Space between buttons */
  flex-wrap: wrap; /* Allow buttons to wrap to the next line on small screens */
}

.button-container button {
  padding: 10px 20px;
  font-size: 16px;
}

@media screen and (max-width: 768px) {
  .button-container {
    flex-direction: column; /* On mobile, stack the buttons vertically */
    align-items: center; /* Center the buttons vertically */
    gap: 10px; /* Space between buttons */
  }

  .button-container button {
    width: 100%; /* Make buttons take full width on small screens */
    padding: 15px;
    font-size: 18px;
  }
}

button {
  background-color: #4CAF50; /* Green background */
  color: white; /* White text */
  border: none; /* Remove border */
  border-radius: 5px; /* Rounded corners */
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer; /* Show pointer cursor on hover */
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049; /* Darker green on hover */
}
